// src\components\storage\storage-sidebar.tsx
'use client';

import * as React from 'react';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { cn } from '@/lib/utils';
import { FileText, Folder, Star, Clock, Upload } from 'lucide-react';
import type { Locale } from '@/i18n-config';

interface StorageSidebarProps {
  subjects: string[];
  selectedSubject: string;
  onSubjectSelect: (subject: string) => void;
  onUploadClick: () => void;
  dictionary: any;
}

export function StorageSidebar({
  subjects,
  selectedSubject,
  onSubjectSelect,
  onUploadClick,
  dictionary
}: StorageSidebarProps) {
  const t = dictionary?.storage || {
    title: 'File Storage',
    allFiles: 'All Files',
    recentFiles: 'Recent Files',
    starredFiles: 'Important Files',
    uploadFile: 'Upload File',
  };

  return (
    <div className="pb-12 w-64">
      <div className="space-y-4 py-4">
        <div className="px-3 py-2">
          <div className="space-y-3">
            <Button 
              variant="secondary" 
              className="w-full justify-start gap-2" 
              onClick={onUploadClick}
            >
              <Upload className="h-4 w-4" />
              {t.uploadFile}
            </Button>
          </div>
        </div>
        <div className="px-3 py-2">
          <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
            Quick Access
          </h2>
          <div className="space-y-1">
            <Button
              variant={selectedSubject === 'all' ? 'secondary' : 'ghost'}
              className="w-full justify-start gap-2"
              onClick={() => onSubjectSelect('all')}
            >
              <FileText className="h-4 w-4" />
              {t.allFiles}
            </Button>
            <Button
              variant={selectedSubject === 'recent' ? 'secondary' : 'ghost'}
              className="w-full justify-start gap-2"
              onClick={() => onSubjectSelect('recent')}
            >
              <Clock className="h-4 w-4" />
              {t.recentFiles}
            </Button>
            <Button
              variant={selectedSubject === 'starred' ? 'secondary' : 'ghost'}
              className="w-full justify-start gap-2"
              onClick={() => onSubjectSelect('starred')}
            >
              <Star className="h-4 w-4" />
              {t.starredFiles}
            </Button>
          </div>
        </div>
        <div className="px-3 py-2">
          <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
            {t.subjects}
          </h2>
          <ScrollArea className="h-[300px] px-1">
            <div className="space-y-1">
              {subjects.map((subject) => (
                <Button
                  key={subject}
                  variant={selectedSubject === subject ? 'secondary' : 'ghost'}
                  className="w-full justify-start gap-2"
                  onClick={() => onSubjectSelect(subject)}
                >
                  <Folder className="h-4 w-4" />
                  {subject}
                </Button>
              ))}
            </div>
          </ScrollArea>
        </div>
      </div>
    </div>
  );
}
