// src\components\storage\storage-header.tsx
'use client';

import * as React from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { AudioRecorder } from './audio-recorder';
import { Upload } from 'lucide-react';

interface StorageHeaderProps {
  onFileUpload: (file: File) => Promise<void>;
  onAudioRecordingComplete: (blob: Blob) => Promise<void>;
  selectedSubject: string;
  uploading: boolean;
  dictionary: any;
}

export function StorageHeader({
  onFileUpload,
  onAudioRecordingComplete,
  selectedSubject,
  uploading,
  dictionary
}: StorageHeaderProps) {
  const [showUploadDialog, setShowUploadDialog] = React.useState(false);
  const fileInputRef = React.useRef<HTMLInputElement>(null);

  const t = dictionary?.storage || {
    uploadFile: 'Upload File',
    dragDropText: 'Drag and drop files here or click to browse',
    uploadButton: 'Upload',
    cancel: 'Cancel'
  };

  const handleFileSelect = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      await onFileUpload(file);
      setShowUploadDialog(false);
    }
  };

  const handleDrop = async (event: React.DragEvent) => {
    event.preventDefault();
    const file = event.dataTransfer?.files[0];
    if (file) {
      await onFileUpload(file);
      setShowUploadDialog(false);
    }
  };

  const handleDragOver = (event: React.DragEvent) => {
    event.preventDefault();
  };

  return (
    <>
      <div className="flex items-center justify-between mb-6">
        <div className="flex gap-4">
          <Button
            onClick={() => setShowUploadDialog(true)}
            disabled={!selectedSubject || uploading}
            className="gap-2"
          >
            <Upload className="h-4 w-4" />
            {t.uploadFile}
          </Button>
          <AudioRecorder onRecordingComplete={onAudioRecordingComplete} />
        </div>
      </div>

      <Dialog open={showUploadDialog} onOpenChange={setShowUploadDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>{t.uploadFile}</DialogTitle>
            <DialogDescription>
              Upload files to your storage
            </DialogDescription>
          </DialogHeader>
          <div
            className="border-2 border-dashed rounded-lg p-12 text-center cursor-pointer hover:bg-secondary/50 transition-colors"
            onClick={() => fileInputRef.current?.click()}
            onDrop={handleDrop}
            onDragOver={handleDragOver}
          >
            <Upload className="h-12 w-12 mx-auto mb-4 text-muted-foreground" />
            <p className="text-muted-foreground">{t.dragDropText}</p>
            <Input
              ref={fileInputRef}
              type="file"
              className="hidden"
              onChange={handleFileSelect}
              multiple={false}
            />
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowUploadDialog(false)}>
              {t.cancel}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
}
