// src\app\[lang]\dashboard\storage\page.tsx
'use client';

import * as React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { ScrollArea } from '@/components/ui/scroll-area';
import { StorageSidebar } from '@/components/storage/storage-sidebar';
import { StorageHeader } from '@/components/storage/storage-header';
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth, db, storage } from '@/lib/firebase';
import { Loader2, Upload } from 'lucide-react';
import type { Locale } from '@/i18n-config';
import { FileGrid } from '@/components/storage/file-grid';
import { FileFilters } from '@/components/storage/file-filters';
import { AudioRecorder } from '@/components/storage/audio-recorder';
import { useToast } from '@/hooks/use-toast';
import {
  collection,
  query,
  where,
  orderBy,
  onSnapshot,
  addDoc,
  updateDoc,
  doc,
  serverTimestamp,
} from 'firebase/firestore';
import {
  ref,
  uploadBytes,
  getDownloadURL,
  deleteObject,
} from 'firebase/storage';

interface StorageFile {
  id: string;
  name: string;
  type: string;
  url: string;
  subject: string;
  createdAt: Date;
  isImportant: boolean;
  thumbnail?: string;
}

interface StoragePageProps {
  params: Promise<{ lang: Locale }>;
  dictionary: any;
}

export default function StoragePage({ params, dictionary }: StoragePageProps) {
  const { lang } = React.use(params);
  const [user] = useAuthState(auth);
  const [files, setFiles] = React.useState<StorageFile[]>([]);
  const [loading, setLoading] = React.useState(true);
  const [uploading, setUploading] = React.useState(false);
  const [subjects, setSubjects] = React.useState<string[]>([]);
  const [selectedSubject, setSelectedSubject] = React.useState('');
  const [selectedFile, setSelectedFile] = React.useState<StorageFile | null>(null);
  const [showFileDialog, setShowFileDialog] = React.useState(false);
  const { toast } = useToast();
  const fileInputRef = React.useRef<HTMLInputElement>(null);

  const t = dictionary?.storage || {
    title: 'مكتبة الملفات',
    selectSubject: 'اختر المادة',
    selectSubjectPlaceholder: 'اختر مادة',
    uploadFile: 'رفع ملف',
    files: 'الملفات',
    noFiles: 'لا توجد ملفات',
    openFile: 'فتح الملف',
    markImportant: 'تمييز كمهم',
    removeImportant: 'إزالة التمييز',
    download: 'تحميل'
  };

  // Load files and subjects
  React.useEffect(() => {
    if (!user) {
      setLoading(false);
      return;
    }

    // Get subjects
    const subjectsQuery = query(collection(db, 'users', user.uid, 'subjects'));
    const unsubSubjects = onSnapshot(subjectsQuery, (snap) => {
      const subjectsList = snap.docs.map(d => d.data().name as string);
      setSubjects(subjectsList);
    });

    // Get files
    const filesQuery = query(
      collection(db, 'users', user.uid, 'files'),
      orderBy('createdAt', 'desc')
    );
    const unsubFiles = onSnapshot(filesQuery, (snap) => {
      const filesList = snap.docs.map((d) => ({
        id: d.id,
        ...d.data(),
        createdAt: d.data().createdAt?.toDate() ?? new Date(),
      })) as StorageFile[];
      setFiles(filesList);
      setLoading(false);
    });

    return () => {
      unsubSubjects();
      unsubFiles();
    };
  }, [user]);

  const uploadToServer = async (file: File | Blob, fileName: string, subject: string) => {
    const formData = new FormData();
    formData.append('file', file, fileName);
    formData.append('subject', subject);
    formData.append('userId', user?.uid || '');
    formData.append('userName', user?.displayName || user?.email?.split('@')[0] || '');

    const response = await fetch('/api/upload', {
      method: 'POST',
      body: formData,
    });

    if (!response.ok) {
      throw new Error('Upload failed');
    }

    const data = await response.json();
    return data.url;
  };

  const handleFileUpload = async (file: File) => {
    if (!user || !selectedSubject) {
      toast({
        title: 'Error',
        description: 'Please select a subject first',
        variant: 'destructive',
      });
      return;
    }

    setUploading(true);
    try {
      const fileName = `${Date.now()}_${file.name}`;
      const url = await uploadToServer(file, fileName, selectedSubject);

      // Create thumbnail for images
      let thumbnail: string | undefined;
      if (file.type.startsWith('image/')) {
        thumbnail = url; // For images, use the same URL as thumbnail
      }

      // Add file metadata to Firestore
      await addDoc(collection(db, 'users', user.uid, 'files'), {
        name: file.name,
        type: file.type,
        url,
        subject: selectedSubject,
        createdAt: serverTimestamp(),
        isImportant: false,
        thumbnail,
      });

      toast({
        title: 'Success',
        description: 'File uploaded successfully',
      });
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.message,
        variant: 'destructive',
      });
    } finally {
      setUploading(false);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  const handleAudioRecordingComplete = async (blob: Blob) => {
    if (!user || !selectedSubject) {
      toast({
        title: 'Error',
        description: 'Please select a subject first',
        variant: 'destructive',
      });
      return;
    }

    setUploading(true);
    try {
      const fileName = `recording_${Date.now()}.webm`;
      const url = await uploadToServer(blob, fileName, selectedSubject);

      await addDoc(collection(db, 'users', user.uid, 'files'), {
        name: fileName,
        type: 'audio/webm',
        url,
        subject: selectedSubject,
        createdAt: serverTimestamp(),
        isImportant: false,
      });

      toast({
        title: 'Success',
        description: 'Recording saved successfully',
      });
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.message,
        variant: 'destructive',
      });
    } finally {
      setUploading(false);
    }
  };

  const handleFileClick = (file: StorageFile) => {
    setSelectedFile(file);
    setShowFileDialog(true);
  };

  const toggleFileImportance = async (file: StorageFile) => {
    if (!user) return;
    try {
      await updateDoc(doc(db, 'users', user.uid, 'files', file.id), {
        isImportant: !file.isImportant,
      });
      toast({
        title: 'Success',
        description: `File marked as ${!file.isImportant ? 'important' : 'not important'}`,
      });
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.message,
        variant: 'destructive',
      });
    }
  };

  const handleFilterChange = (filters: {
    search: string;
    subject: string;
    type: string;
    date: Date | undefined;
    isImportant: boolean;
  }) => {
    if (!user) return;

    let baseQuery = query(collection(db, 'users', user.uid, 'files'));
    const conditions = [];

    if (filters.subject && filters.subject !== 'all') {
      conditions.push(where('subject', '==', filters.subject));
    }

    if (filters.type && filters.type !== 'all') {
      if (filters.type === 'other') {
        const commonTypes = ['image/*', 'video/*', 'audio/*', 'application/pdf'];
        conditions.push(where('type', 'not-in', commonTypes));
      } else {
        const typePrefix = filters.type.split('/')[0] + '/';
        conditions.push(where('type', 'array-contains', typePrefix));
      }
    }

    if (filters.date) {
      const startOfDay = new Date(filters.date);
      startOfDay.setHours(0, 0, 0, 0);
      const endOfDay = new Date(filters.date);
      endOfDay.setHours(23, 59, 59, 999);
      conditions.push(where('createdAt', '>=', startOfDay));
      conditions.push(where('createdAt', '<=', endOfDay));
    }

    if (filters.isImportant) {
      conditions.push(where('isImportant', '==', true));
    }

    if (conditions.length > 0) {
      baseQuery = query(baseQuery, ...conditions, orderBy('createdAt', 'desc'));
    }

    // Apply search filter in memory since Firestore doesn't support text search
    const unsubFiles = onSnapshot(baseQuery, (snap) => {
      let filesList = snap.docs.map((d) => ({
        id: d.id,
        ...d.data(),
        createdAt: d.data().createdAt?.toDate() ?? new Date(),
      })) as StorageFile[];

      if (filters.search) {
        const searchLower = filters.search.toLowerCase();
        filesList = filesList.filter(
          file => file.name.toLowerCase().includes(searchLower) ||
                 file.subject.toLowerCase().includes(searchLower)
        );
      }

      setFiles(filesList);
    });

    return unsubFiles;
  };

  if (!user || loading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <Loader2 className="h-8 w-8 animate-spin" />
      </div>
    );
  }

  return (
    <div className="flex flex-col h-[calc(100vh-4rem)]">
      <div className="flex flex-1">
        <StorageSidebar
          subjects={subjects}
          selectedSubject={selectedSubject}
          onSubjectSelect={setSelectedSubject}
          onUploadClick={() => fileInputRef.current?.click()}
          dictionary={dictionary}
        />
        <div className="flex-1 overflow-auto">
          <div className="h-full px-8 py-6">
            <StorageHeader
              onFileUpload={handleFileUpload}
              onAudioRecordingComplete={handleAudioRecordingComplete}
              selectedSubject={selectedSubject}
              uploading={uploading}
              dictionary={dictionary}
            />

            <FileFilters
              subjects={subjects}
              onFilterChange={handleFilterChange}
            />

            <Card className="mt-6">
              <CardHeader>
                <CardTitle className="font-headline">{t.title}</CardTitle>
              </CardHeader>
              <CardContent>
                {files.length === 0 ? (
                  <div className="flex flex-col items-center justify-center h-40 text-muted-foreground">
                    <Upload className="h-12 w-12 mb-2" />
                    <p>{t.noFiles}</p>
                  </div>
                ) : (
                  <FileGrid files={files} onFileClick={handleFileClick} />
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </div>

      {showFileDialog && selectedFile && (
        <Dialog open={showFileDialog} onOpenChange={setShowFileDialog}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>{selectedFile.name}</DialogTitle>
              <DialogDescription>{selectedFile.subject}</DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              {selectedFile.type.startsWith('image/') ? (
                <div className="rounded-lg overflow-hidden">
                  <img src={selectedFile.url} alt={selectedFile.name} className="w-full" />
                </div>
              ) : selectedFile.type.startsWith('video/') ? (
                <video src={selectedFile.url} controls className="w-full rounded-lg" />
              ) : selectedFile.type.startsWith('audio/') ? (
                <audio src={selectedFile.url} controls className="w-full" />
              ) : (
                <div className="flex justify-center">
                  <Button asChild>
                    <a href={selectedFile.url} target="_blank" rel="noopener noreferrer">
                      <Upload className="mr-2 h-4 w-4" />
                      {t.openFile}
                    </a>
                  </Button>
                </div>
              )}
              <div className="flex justify-between items-center">
                <Button
                  variant="outline"
                  onClick={() => toggleFileImportance(selectedFile)}
                >
                  {selectedFile.isImportant ? t.removeImportant : t.markImportant}
                </Button>
                <Button asChild variant="secondary">
                  <a href={selectedFile.url} download>
                    {t.download}
                  </a>
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      )}
    </div>
  );
}
